header {
	padding: 0 15px; }
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 29px 0 33px 0;
    &-social {
        display: flex;
        a {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 35px;
            height: 35px;
            border-radius: 50%;
            text-decoration: none;
            margin-right: 5px;
            color: #D9435D;
            transition: 0.3s;
            box-sizing: border-box;
            &:last-child {
                margin-right: 0; }
            i {
                font-size: 18px; } }
        &__vk {
            &:hover {
                background:  #4C75A3;
                color: #ffffff;
                i {
                    transform: rotate(-15deg); } } }
        &__fb {
            &:hover {
                background: #3B5998;
                color: #ffffff;
                i {
                    transform: rotate(-15deg); } } }
        &__tw {
            &:hover {
                background: #1DA1F2;
                color: #ffffff;
                i {
                    transform: rotate(-15deg); } } }
        &__ok {
            &:hover {
                background: #ED812B;
                color: #ffffff;
                i {
                    transform: rotate(-15deg); } } }
        &__yt {
            &:hover {
                background: #FF0000;
                color: #ffffff;
                i {
                    transform: rotate(-15deg); } } }
        &__inst {
            &:hover {
                background: linear-gradient(226.74deg, #405DE6 17.66%, #5851DB 23.56%, #833AB4 31.68%, #C13584 40.16%, #E1306C 48.64%, #FD1D1D 56.76%, #F56040 64.87%, #F77737 70.04%, #FCAF45 77.78%, #FFDC80 84.42%);
                color: #ffffff;
                i {
                    transform: rotate(-15deg); } } } }
    &-logo {
        display: flex;
        align-items: center;
        span {
            display: block;
            max-width: 157px;
            font-family: Arsenal Regular;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 23px;
            color: #828282;
            margin-left: 24px; } }
    &-account {
        a {
            display: flex;
            align-items: center;
            text-decoration: none;
            margin-bottom: 17px;
            &:last-child {
                margin-bottom: 0; }
            span {
                font-family: Arimo Regular;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 18px;
                color: #494A4C;
                margin-left: 10px; } } }
    &-mobile {
        display: none;
        justify-content: space-between;
        align-items: center;
        padding: 11px 15px;
        border-bottom: 1px solid #D9435D;
        box-sizing: border-box;
        background: #ffffff;
        z-index: 100;
        position: fixed;
        width: 100%;
        height: 50px;
        top: 0;
        left: 0;
        &__burger {
            width: 21px;
            height: 21px;
            span {
                display: block;
                width: 100%;
                height: 4px;
                background: #D9435D;
                margin-bottom: 4px;
                border-radius: 5px;
                &:last-child {
                    margin-bottom: 0; } } }
        &__account {
            display: flex;
            align-items: center;
            a {
                text-decoration: none;
                margin-right: 10px;
                position: relative;
                &:last-child {
                    margin-right: 0; }
                span {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    top: -15px;
                    right: -18px;
                    width: 24px;
                    height: 24px;
                    font-family: Arimo Regular;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 16px;
                    text-align: center;
                    color: #FFFFFF;
                    border-radius: 50%;
                    background: #7E2A2A; } } }
        &__menu {
            display: none;
            position: fixed;
            width: 100%;
            height: 100vh;
            top: 0;
            left: 0;
            background: #ffffff;
            z-index: 99;
            * {
                outline: none; } }
        &__close {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            height: 62px;
            border-bottom: 1px solid rgba(217, 67, 93, 0.1);
            padding: 0 15px;
            i {
                color: #D9435D;
                font-size: 24px; } }
        &__list {
            padding: 18px 15px;
            ul {
                list-style: none;
                padding: 0;
                li {
                    a {
                        font-family: Arsenal Bold;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 17px;
                        line-height: 34px;
                        color: #D9435D;
                        text-decoration: none; } } } }
        &__social {
            padding: 16px 15px;
            border-top: 1px solid rgba(217, 67, 93, 0.1);
            a {
                text-decoration: none;
                margin-right: 20px;
                i {
                    color: #D9435D;
                    font-size: 24px; } } }
        &__search {
            &-block {
                display: none;
                position: fixed;
                top: 0;
                left: 0;
                width: 100%;
                height: 100vh;
                background: #ffffff;
                z-index: 99; }
            &-close {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                height: 62px;
                padding: 0 15px;
                box-sizing: border-box;
                i {
                    color: #D9435D;
                    font-size: 24px; } }
            &-box {
                width: 100%;
                position: relative;
                padding: 0 15px;
                box-sizing: border-box;
                input {
                    width: 100%;
                    height: 42px;
                    background: #FFFFFF;
                    border: 1px solid #D0C9D6;
                    box-sizing: border-box;
                    border-radius: 5px;
                    font-family: Arsenal Regular;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 17px;
                    line-height: 34px;
                    color: #979899;
                    padding: 0 50px 0 15px;
                    &:focus {
                        outline: none; } }
                svg {
                    position: absolute;
                    top: 11px;
                    right: 25px;
                    z-index: 99; } } } } }
@keyframes navBottom {
    0% {
        height: 0px; }
    50% {
        height: 5px; }
    75% {
        height: 5px; }
    100% {
        height: 5px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px; } }

@-webkit-keyframes navBottom {
    0% {
        height: 0px; }
    50% {
        height: 5px; }
    75% {
        height: 5px; }
    100% {
        height: 5px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px; } }

@keyframes navBtn {
    0% {
        opacity: 1; }
    50% {
        opacity: 0.5; }
    100% {
        opacity: 0; } }

@keyframes search {
    0% {
        height: 0px;
        width: 0px; }
    25% {
        height: 50px;
        width: 0px; }
    50% {
        height: 50px;
        width: 740px;
        padding: 15px;
        border-radius: 0px; }
    100% {
        height: 50px;
        width: 740px;
        padding: 15px;
        border-radius: 5px; } }

@keyframes searchClose {
    0% {
        height: 50px;
        width: 740px;
        padding: 15px;
        border-radius: 5px; }
    25% {
        height: 50px;
        width: 740px;
        padding: 15px;
        border-radius: 0px; }
    50% {
        height: 50px;
        width: 0px; }
    100% {
        height: 0px;
        width: 0px; } }

.navbar-button {
    animation: navBtn 0.5s ease forwards; }

.search-open {
    animation: search 1s ease-in forwards; }
.search-close {
    animation: searchClose 1s ease-in forwards; }
.nav {
    background-image: url('../img/bg-navbar.jpg'); }
.navbar {
    ul {
        display: flex;
        justify-content: center;
        padding: 16px 0 13px;
        margin: 0;
        list-style: none; }
    a {
        font-family: Arsenal Regular;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 25px;
        color: #FFFFFF;
        text-decoration: none;
        padding: 16px 30px 16px;
        position: relative;
        &:hover {
            &:before {
                -webkit-animation: navBottom 0.5s forwards;
                // height: 5px
                // border-top-left-radius: 10px
 } }                // border-top-right-radius: 10px
        &:before {
            position: absolute;
            content: '';
            width: 100%;
            height: 0px;
            bottom: 0;
            left: 0;
            background: #D9435D;
            transition: 0.3s; } }
    &-item {
        position: relative;
        &:last-child {
            &:hover {
                a {
                    path {
                        fill: #D9435D; } } }
            a {
                padding: 18px 30px 14px; } } }
    &-search {
        position: absolute;
        top: 50%;
        right: 0;
        width: 0px;
        height: 0px;
        margin-top: -27px;
        padding: 0;
        background: #F5F9FA;
        border-radius: 0;
        font-family: Arimo Regular;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 21px;
        color: #494A4C;
        border: none;
        outline: none;
        &__close {
            position: absolute;
            display: none;
            top: 0;
            right: 12px;
            color: #D0C9D6;
            font-size: 24px;
            cursor: pointer;
            z-index: 5;
            transition: 0.5s;
            &:hover {
                color: #D9435D; } } }

    &-blocks {
        display: flex;
        justify-content: space-between;
        padding: 33px 0 40px; }
    &-block {
        width: 100%;
        max-width: 602px;
        background: #ffffff;
        box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.18);
        border-bottom: 5px solid #D9435D;
        padding-bottom: 52px;
        position: relative;
        &:last-child {
            border-bottom: 5px solid #9B51E0; }
        &__text {
            padding-left: 42px; }
        &__image {
            width: 100%;
            max-height: 299px;
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover; } }
        &__title {
            font-family: Arsenal Bold;
            font-style: normal;
            font-weight: bold;
            font-size: 32px;
            line-height: 40px;
            color: #494A4C;
            border-radius: 6px;
            margin: 12px 0 11px; }
        &__subtitle {
            font-family: Arimo Regular;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 27px;
            color: #494A4C;
            border-radius: 6px; }
        &__bone {
            display: block;
            position: absolute;
            bottom: 27.5%;
            right: 30px;
            span {
                position: absolute;
                top: 9px;
                left: 50%;
                transform: translateX(-50%);
                font-family: Roboto Regular;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 16px;
                display: flex;
                align-items: center;
                text-align: center;
                color: #FFFFFF; } }
        &__link {
            position: absolute !important;
            width: 100%;
            height: 100%;
            padding: 0 !important;
            &:before {
                display: none; }
            &:after {
                display: none; } } } }

.nav-fixed {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1);
    padding: 12px 0 5px;
    z-index: 999; }
.navbar-fixed {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__logo {
        display: block;
        width: 215px;
        height: 59px;
        img {
            width: auto;
            max-width: 100%;
            height: auto;
            max-height: 100%; } }
    &__menu {
        ul {
            display: flex;
            padding: 0;
            margin: 0;
            list-style: none;
            li {
                &:last-child {
                    position: relative;
                    a {
                        &:before {
                            bottom: -6px; } } } } }
        a {
            font-family: Arsenal Regular;
            font-style: normal;
            font-weight: normal;
            font-size: 20px;
            line-height: 25px;
            color: #494A4C;
            text-decoration: none;
            padding: 16px 30px 16px;
            position: relative;
            &:hover {
                &:before {
                    -webkit-animation: navBottom 0.5s forwards;
                    // height: 5px
                    // border-top-left-radius: 10px
 } }                    // border-top-right-radius: 10px
            &:before {
                position: absolute;
                content: '';
                width: 100%;
                height: 0px;
                bottom: -8px;
                left: 0;
                background: #D9435D;
                transition: 0.3s; } } }
    &__account {
        a {
            text-decoration: none;
            position: relative;
            margin-right: 25px;
            &:last-child {
                margin-right: 0; } }
        span {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: -15px;
            right: -18px;
            width: 24px;
            height: 24px;
            font-family: Arimo Regular;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            text-align: center;
            color: #FFFFFF;
            border-radius: 50%;
            background: #7E2A2A; } } }

//Адаптив
@media screen and (max-width: 1250px) {
	.navbar {
		&-fixed {
			padding: 12px 0;
			justify-content: center;
			&__logo {
				display: none; }
			&__menu {
				a {
					padding: 16px 20px 16px; } }
			&__account {
				a {
					margin-right: 35px; } } } } }
@media screen and (max-width: 1200px) {
    .navbar {
        &-block {
            width: 460px;
            height: 375px;
            &__text {
                padding: 0 20px; } } } }
@media screen and (max-width: 999px) {
    .header {
        display: none;
        &-logo {
            span {
                display: none; } }
        &-mobile {
            display: flex; } }
    .nav {
        margin-top: 50px; }
    .navbar {
        ul {
            display: none; }
        &-fixed {
            display: none; } }
    .navbar {
        &-blocks {
            justify-content: space-around;
            flex-wrap: wrap; }
        &-block {
            width: 302px;
            height: 261px;
            box-sizing: border-box;
            &__title {
                font-size: 24px;
                line-height: 30px;
                margin: 10px 0 0; }
            &__subtitle {
                font-size: 14px;
                line-height: 21px; }
            &__bone {
                bottom: 90px;
                right: 5px; } } }
    .navbar-fixed__menu {
        ul {
            li {
                a {
                    padding: 16px 10px; } } } }
    .navbar-fixed__account a {
        &:last-child {
            margin-right: 0; } } }

@media screen and (max-width: 768px) {
    header {
        padding: 0; }
    .nav-fixed {
        padding: 0; } }
@media screen and (max-width: 635px) {
    .navbar {
        &-block {
            margin-bottom: 10px; } } }
