.breadcrumbs {
	text-align: center;
	position: relative;
	&:after {
		position: absolute;
		content: url('../img/sprites/intermediate-svg/breadcrumbs-bottom.svg');
		bottom: -13px;
		left: 50%;
		margin-left: -45px; }
	&-item {
		font-family: Arsenal Regular;
		font-style: normal;
		font-weight: normal;
		font-size: 14px;
		line-height: 18px;
		text-align: center;
		color: #D9435D;
		text-decoration: none;
		margin-right: 20px;
		position: relative;
		&:after {
			position: absolute;
			content: '/';
			top: 0;
			right: -12px;
			color: #494A4C; }
		&:before {
			position: absolute;
			content: '';
			width: 0;
			height: 1px;
			bottom: 0;
			right: 0;
			background: #D9435D;
			transition: 0.3s ease-in-out; }
		&:hover {
			&:before {
				left: 0;
				right: auto;
				width: 100%; } }
		&:last-child {
			&:after {
				display: none; }
			&:before {
				display: none; }
			&:hover {
